main .content-stripe {
  height: 100%;
  padding-block: 50px;
}

a.active {
  color: #ff9109;
}

a:hover {
  color: #b15500;
}

.user-window {
  background-color: #efefef;
  width: 100%;
  max-width: 800px;
  margin: auto;
  padding: 40px;
  display: flex;
}

.user-menu {
  width: 30%;
  padding-right: 30px;
}

.user-photo {
  position: relative;
}

.photo-frame img {
  border-radius: 50%;
  width: 100%;
}

.change-photo img {
  border-radius: initial;
  width: 50%;
  margin-top: 3px;
}

.change-photo {
  background: #fff;
  border: 1px dashed #52ce58;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.user-nav-items {
  margin-top: 40px;
  margin-left: 20px;
}

nav {
  font-weight: 500;
}

.user-nav-items li {
  margin-block: 20px;
}

.settings {
  margin-left: 30px;
}

.settings-profile {
  width: 60%;
}

.settings-contribution {
  width: calc(70% - 30px);
}

.settings section {
  padding-inline: 10px;
}

.settings article {
  margin-bottom: 30px;
}

.settings h2 {
  text-align: left;
  margin-bottom: 10px;
  font-weight: 400;
}

.settings form {
  text-align: right;
}

.settings button {
  margin-right: 40px;
  padding: 6px 15px;
}

.delimiter {
  margin-block: 24px;
  transform: rotate(3deg);
}

.password-info {
  text-align: left;
  margin-bottom: 10px;
  font-size: .8em;
  font-style: italic;
}

li > img {
  width: 24px;
  height: 24px;
}

.project-thumbs {
  flex-wrap: wrap;
  gap: 30px 5%;
  margin-top: 30px;
  display: flex;
}

.project-box {
  text-align: center;
  flex-direction: column;
  justify-content: space-between;
  width: 30%;
  padding: 5px;
  display: flex;
}

.project-thumbs .project-box {
  cursor: pointer;
}

.project-thumbs .project-box:hover {
  border-radius: 10px;
  transition: all .1s;
  transform: translateY(2px);
  box-shadow: 0 0 3px #bcbcbc;
}

.project-box.done .project-image:after {
  content: "";
  background-color: green;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.project-image {
  width: 80%;
  margin-inline: auto;
  position: relative;
}

.project-image img {
  object-fit: cover;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  transition: all .2s ease-out;
}

.project-box h3 {
  margin-top: 20px;
}

.resources {
  display: flex;
}

.resources li {
  width: 30px;
  height: 30px;
}

.project-details {
  background-color: #fff;
  border-radius: 7px;
  justify-content: space-between;
  margin-top: 20px;
  padding: 20px 15px;
  display: flex;
}

.project-details .project-box:hover .project-image img {
  border-top-right-radius: 10px;
}

.project-description {
  width: 70%;
  margin-block: 20px;
  padding-inline: 20px;
}

.description-header {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.description-header h3 {
  font-size: 1em;
  font-weight: 400;
}

.description-header h3 span {
  font-size: 1.2em;
  font-weight: 500;
}

.description-header > span {
  font-size: .8em;
}

.description-text {
  margin-block: 20px;
}

.description-text p {
  text-align: justify;
}

.description-resources li {
  align-items: center;
  display: flex;
}

.description-resources span {
  margin-left: 20px;
}

.description-done {
  color: green;
  margin-top: 20px;
  font-size: .8em;
  font-weight: 500;
}
/*# sourceMappingURL=profile.css.map */
